import * as React from "react"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"
import PlainTile from "../components/common/plainTile"

const IndexPage = () => (
  <Layout>
    <Seo title="Home" />
    {/* <h1>Hi people</h1>
    <p>Welcome to your new Gatsby site.</p>
    <p>Now go build something great.</p>
    <StaticImage
      src="../images/gatsby-astronaut.png"
      width={300}
      quality={95}
      formats={["auto", "webp", "avif"]}
      alt="A Gatsby astronaut"
      style={{ marginBottom: `1.45rem` }}
    />
    <p>
      <Link to="/page-2/">Go to page 2</Link> <br />
      <Link to="/using-typescript/">Go to "Using TypeScript"</Link>
    </p> */}
    <section className="page-section clearfix">
      <div className="container">
        <div className="intro">
          <StaticImage
            className="intro-img img-fluid mb-3 mb-lg-0 rounded"
            src="../images/WhyWoodPools32020.jpg"
            quality={95}
            placeholder="blurred"
            formats={["auto", "webp", "avif"]}
            alt="wood pool image"
          />
          <div className="intro-text left-0 text-center bg-faded p-5 rounded">
            <h2 className="section-heading mb-4">
              <span className="section-heading-upper">
                Environmentally friendly pools
              </span>
              <span className="section-heading-lower">Why wood pools?</span>
            </h2>
            <p className="mb-3">
              Wood has natural qualities which are well-suited to pool sidewalls
              -- it is easily handled and easily worked, it can be modified
              on-site, and it offers resilience to help prevent damage caused by
              ground freezing and thawing.
            </p>
            <div className="intro-button mx-auto">
              <a className="btn btn-primary btn-xl" href="#!">
                Visit Us Today!
              </a>
            </div>
          </div>
        </div>
      </div>
    </section>
    <PlainTile />
  </Layout>
)

export default IndexPage
